<template>
  <div class="cart">
    <!-- 顶部 -->
    <div class="detail_top">
      <van-row>
        <van-col span="4">
          <!-- <van-icon name="arrow-left" class="icon" size="24" @click="$router.go(-1)" color="#fff" /> -->
        </van-col>
        <van-col span="16">
          <h3>我的购物车</h3>
        </van-col>
        <van-col span="4">
          <van-icon name="edit" class="icon" size="24" @click="manage" color="#fff" />
        </van-col>
      </van-row>
    </div>
    <!-- 顶部 end -->

    <template v-if="allShocartData.length > 0">
      <!-- 主体 -->
      <div class="cartlist">
        <van-list
          finished-text="已经加载完啦！"
          :finished="isFinished"
          v-model="isLoading"
          @load="loadData"
        >
          <div class="cart_box" v-for="(item, index) in shopcartData" :key="index">
            <van-swipe-cell :disabled="!isManage">
              <van-row>
                <van-col span="2" offset="1">
                  <van-checkbox
                    class="btn"
                    v-model="item.isChecked"
                    @change="simpleSelect(item)"
                    checked-color="#1989fa"
                  ></van-checkbox>
                </van-col>
                <van-col span="8" @click="goPage(item.pid)">
                  <img :src="item.small_img" alt />
                </van-col>
                <van-col span="13">
                  <p class="title">{{item.name}}</p>
                  <span class="text">{{item.rule}}</span>
                  <p>{{item.enname}}</p>
                  <div>
                    <van-row>
                      <van-col span="4">
                        <span class="price">￥{{item.price}}</span>
                      </van-col>

                      <van-col span="12" offset="8">
                        <span class="iconfont icon-jianshao icon" @click="decrease(item)"></span>
                        <input type="text" readonly v-model="item.count" />
                        <span class="iconfont icon-jia icon" @click="increase(item)"></span>
                      </van-col>
                    </van-row>
                  </div>
                </van-col>
              </van-row>
              <template #right>
                <van-button
                  square
                  text="删除"
                  type="danger"
                  class="delete-button"
                  @click="removeOneShopcart(item.sid, index)"
                />
              </template>
            </van-swipe-cell>
          </div>
        </van-list>
      </div>
      <!-- 主体 end-->

      <!-- 结算/删除 -->

        <van-submit-bar
          :price="total"
          button-text="提交订单"
          button-type="info"
          :safe-area-inset-bottom="true"
          v-show="!isManage"
          @submit="Jump"
        >
          <van-checkbox v-model="isAllSelect" @click="allSelect">全选</van-checkbox>
        </van-submit-bar>

        <van-submit-bar
          :price="total"
          button-text="删除"
          button-type="info"
          :safe-area-inset-bottom="true"
          v-show="isManage"
          @submit="removeMoreShopcart"
        >
          <van-checkbox v-model="isAllSelect">全选</van-checkbox>
        </van-submit-bar>

      <!-- 结算/删除 end -->
    </template>

    <template v-else>
      <div>
        <div class="not-shopcart">
          <div class="not-shopcart-icon">
            <van-icon name="cart-o" />
          </div>
          <div v-if="isLogin">购物车没有商品，赶紧去逛一逛！</div>
          <div v-else>
            <span class="login-text" @click="goState">登录，查看购物车数据</span>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { mapState } from "vuex";

import { utils } from "../utils/utils";

export default {
  naem: "Shopcart",
  data() {
    return {
      //触底加载提示
      isLoading: false,

      // 全选标识
      isAllSelect: false
    };
  },

  created() {
    this.$store.state.vanTabbar = true;

    this.$store.commit("shopcartModule/emptyData");

    // 获取购物车所有商品
    this.getAllShopcartProduct();
  },

  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    },

    ...mapState("shopcartModule", [
      "allShocartData",
      "shopcartData",
      "isFinished",
      "count",
      "isManage",
      "address",
      "total"
    ])
  },

  methods: {
    // 获取购物车所有商品
    getAllShopcartProduct() {
      // 获取token字符串
      let tokenString = localStorage.getItem("_t");

      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          

          if (result.data.code == 5000) {
            //添加判断勾选的属性
            result.data.result.forEach(v => {
              v.isChecked = false;
            });

            this.$store.commit("shopcartModule/changeData", {
              key: "allShocartData",
              value: result.data.result
            });

            
          }
        })
        .catch(err => {
          
        });
    },

    // 加载商品数据
    loadData() {
      setTimeout(() => {
        // 
        this.isLoading = false;

        if (this.shopcartData.length >= this.allShocartData.length) {
          
          //没有数据可加载
          this.$store.commit("shopcartModule/changeData", {
            key: "isFinished",
            value: true
          });

          return;
        }

        this.$store.commit("shopcartModule/loadShopcartData");
      }, 1000);
    },

    // 修改购物车商品数量
    modifyShopcartCount(sid, count) {
      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "处理中..."
      });
      //获取token字符串
      let tokenString = localStorage.getItem("_t");

      this.axios({
        method: "POST",
        url: "/modifyShopcartCount",
        data: {
          appkey: this.appkey,
          tokenString,
          sid,
          count
        },
        transformRequest: utils.transformRequest
      })
        .then(result => {
          this.$toast.clear();
          // 
          this.sum();
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    // 增加商品数量
    increase(item) {
      
      ++item.count;

      this.modifyShopcartCount(item.sid, item.count);
    },

    // 减少数量
    decrease(item) {
      if (item.count == 1) {
        return;
      }

      --item.count;

      this.modifyShopcartCount(item.sid, item.count);
    },

    //管理商品
    manage() {
      this.$store.commit("shopcartModule/changeData", {
        key: "isManage",
        value: !this.isManage
      });
    },

    // 全选
    allSelect() {
      this.shopcartData.forEach(v => {
        v.isChecked = !this.isAllSelect;
      });
    },

    // 单选
    simpleSelect(item) {
      

      this.sum();

      if (!item.isChecked) {
        //没有全选
        this.isAllSelect = false;
        return;
      }

      for (let i = 0; i < this.shopcartData.length; i++) {
        if (!this.shopcartData[i].isChecked) {
          //没有全选
          this.isAllSelect = false;
          return;
        }
      }

      this.isAllSelect = true;
    },

    //删除单个商品
    removeOneShopcart(sid, index) {
      let sids = JSON.stringify([sid]);

      this.removeShopcart(sids)
        .then(result => {
          this.$toast.clear();
          
          this.$store.commit("shopcartModule/removeShopcartData", [sid]);
          this.sum();
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //删除多个商品
    removeMoreShopcart() {
      //获取勾选的商品
      let sids = [];
      this.shopcartData.forEach(v => {
        if (v.isChecked) {
          sids.push(v.sid);
        }
      });

      if (sids.length == 0) {
        this.$toast({
          message: "没有选择商品"
        });
        return;
      }

      // 
      this.removeShopcart(JSON.stringify(sids))
        .then(result => {
          this.$toast.clear();
          
          this.$store.commit("shopcartModule/removeShopcartData", sids);
          this.sum();
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //删除商品
    removeShopcart(sids) {
      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "处理中..."
      });
      //获取token字符串
      let tokenString = localStorage.getItem("_t");

      return this.axios({
        method: "POST",
        url: "/deleteShopcart",
        data: {
          appkey: this.appkey,
          tokenString,
          sids
        },
        transformRequest: utils.transformRequest
      });
    },

    //计算订单总价
    sum() {
      let total = 0;
      this.shopcartData.forEach(v => {
        if (v.isChecked) {
          total += v.price * v.count * 100;
        }
      });

      this.$store.commit("shopcartModule/changeData", {
        key: "total",
        value: total
      });
    },

    //查看详情
    goPage(pid) {
      this.$router.push({ name: "Details", params: { pid } });
    },

    // 转订单页/提交订单
    Jump() {
      // 获取勾选商品的购物车的sid
      let sids = [];
      this.shopcartData.forEach(v => {
        if (v.isChecked) {
          sids.push(v.sid);
        }
      });

      if (sids.length == 0) {
        this.$toast(this.address);
        return;
      }
      this.$router.push({ name: "Pay", query: { sids: sids.join("-") } });
    },

    goState() {
      this.$router.push({ name: "Login" });
    }
  }
};
</script>

<style lang="less" scoped>
.cart {
  background-color: #f9f9f9;
  // position: fixed;
  width: 100%;
  height: 100%;
  .detail_top {
    background-color: #0197fe;
    text-align: center;
    .icon {
      line-height: 40px;
    }
    h3 {
      line-height: 40px;
      color: #fff;
      font-weight: 400;
    }
  }
  .not-shopcart {
    font-size: 18px;
    margin-top: 50px;
    color: #e0e0e0;
    text-align: center;
  }
  .not-shopcart-icon {
    font-size: 64px;
  }

  .login-text {
    color: #444;
  }
  .cartlist {
    // margin-bottom: 50px;
    .cart_box {
      margin: 10px;
      background-color: #fff;
      box-shadow: 0px 0px 2px #ccc;
      border-radius: 10px;
      .btn {
        margin-top: 40px;
      }
      .title {
        line-height: 2;
        color: #333;
        font-size: 16px;
        font-weight: 700;
      }
      .text {
        font-size: 12px;
        color: #5c5c5c;
      }
      .price {
        line-height: 1.5;
        color: #343333;
        display: inline-block;
        margin-right: 20px;
      }
      .icon {
        color: #333;
        font-size: 16px;
      }
      img {
        width: 95px;
        height: 95px;
      }
      p {
        font-size: 12px;
        color: #999;
      }
      input {
        width: 25px;
        margin: 0 10px;
        border: 0;
        text-align: center;
      }
    }

    .delete-button {
      background-color: #893e23;
      height: 100%;
    }
  }
}

/deep/ .van-submit-bar{
  bottom: 50px;
}

/deep/ .van-button--info{
  background-color: #36C970;
}
</style>